.button {
  position: relative;
  display: inline-block;
  border: 1px solid #001E63;
  border-radius: 0.25rem;
  transition: 0.3s;
  line-height: 1;
  padding: 0.75rem 2.4375rem 0.75rem 1.5rem;
}

.button--noline {
  padding: 0;
}

.button--noline::after {
  position: absolute;
  bottom: -3px;
  left: 0;
  content: "";
  width: 120%;
  height: 1px;
  background: #001E63;
  transform: scale(0, 1);
  transform-origin: left top;
  transition: transform 0.3s;
}

.button--noline:hover::after {
  transform: scale(1, 1);
}

.button span {
  display: inline-block;
  font-size: 0.875rem;
  font-family: "Roboto", sans-serif;
  color: #001E63;
  font-weight: 300;
  text-transform: uppercase;
}

.button span::after {
  position: absolute;
  content: "";
  width: 0.4375rem;
  height: 0.4375rem;
  border-top: 1px solid #001E63;
  border-right: 1px solid #001E63;
  -webkit-transform: rotate(45deg);
  transform: rotate(45deg);
  display: inline-block;
  top: 1.125rem;
  right: 1.0625rem;
  transition: 0.3s;
}

.button--noline span::after {
  position: absolute;
  content: "";
  width: 0.4375rem;
  height: 0.4375rem;
  border-top: 1px solid #001E63;
  border-right: 1px solid #001E63;
  -webkit-transform: rotate(45deg);
  transform: rotate(45deg);
  display: inline-block;
  top: 0.375rem;
  right: -0.625rem;
  transition: 0.3s;
}

.button--next span::after {
  position: absolute;
  content: "";
  width: 0.4375rem;
  height: 0.4375rem;
  border-top: 1px solid #001E63;
  border-right: 1px solid #001E63;
  -webkit-transform: rotate(45deg);
  transform: rotate(45deg);
  display: inline-block;
  top: 1.125rem;
  right: 1.0625rem;
  transition: 0.3s;
}

.button--noline:hover.button--noline span::after {
  right: -0.9375rem;
}

.button--noline {
  border: none;
}

.button--noline span {
  padding: 0;
}

